"use client";

import React, { useState } from "react";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogTrigger } from "@/components/ui/dialog";
import { Badge } from "@/components/ui/badge";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";

const dentistsData = [
  {
    id: 1,
    name: "Dr. Sarah Johnson",
    specialty: "General Dentistry",
    experience: "8 years",
    phone: "(555) 111-2222",
    email: "sarah.johnson@dentalclinic.com",
    schedule: "Mon-Fri: 9AM-5PM",
    status: "Available",
    patients: 156,
    rating: 4.9,
    qualifications: ["DDS", "Cosmetic Dentistry Certificate"],
  },
  {
    id: 2,
    name: "Dr. Michael Brown",
    specialty: "Orthodontics",
    experience: "12 years",
    phone: "(555) 333-4444",
    email: "michael.brown@dentalclinic.com",
    schedule: "Tue-Sat: 8AM-4PM",
    status: "Available",
    patients: 203,
    rating: 4.8,
    qualifications: ["DDS", "Orthodontics Residency", "Invisalign Certified"],
  },
  {
    id: 3,
    name: "Dr. Lisa Anderson",
    specialty: "Oral Surgery",
    experience: "15 years",
    phone: "(555) 555-6666",
    email: "lisa.anderson@dentalclinic.com",
    schedule: "Mon-Thu: 7AM-3PM",
    status: "On Leave",
    patients: 89,
    rating: 4.7,
    qualifications: ["DDS", "Oral Surgery Residency", "Implant Specialist"],
  },
  {
    id: 4,
    name: "Dr. James Wilson",
    specialty: "Pediatric Dentistry",
    experience: "6 years",
    phone: "(555) 777-8888",
    email: "james.wilson@dentalclinic.com",
    schedule: "Mon-Fri: 10AM-6PM",
    status: "Available",
    patients: 124,
    rating: 4.9,
    qualifications: ["DDS", "Pediatric Dentistry Residency"],
  },
];

const specialties = [
  "General Dentistry",
  "Orthodontics",
  "Oral Surgery",
  "Pediatric Dentistry",
  "Endodontics",
  "Periodontics",
  "Prosthodontics",
];

export default function DentistsPage() {
  const [searchTerm, setSearchTerm] = useState("");
  const [isDialogOpen, setIsDialogOpen] = useState(false);

  const filteredDentists = dentistsData.filter(dentist =>
    dentist.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
    dentist.specialty.toLowerCase().includes(searchTerm.toLowerCase())
  );

  const handleAddDentist = (e: React.FormEvent) => {
    e.preventDefault();
    setIsDialogOpen(false);
  };

  const getStatusColor = (status: string) => {
    switch (status) {
      case "Available":
        return "default";
      case "Busy":
        return "secondary";
      case "On Leave":
        return "destructive";
      default:
        return "secondary";
    }
  };

  return (
    <div className="space-y-6">
      <div className="flex justify-between items-center">
        <h1 className="text-3xl font-bold text-gray-900">Dentists</h1>
        <Dialog open={isDialogOpen} onOpenChange={setIsDialogOpen}>
          <DialogTrigger asChild>
            <Button>Add New Dentist</Button>
          </DialogTrigger>
          <DialogContent className="sm:max-w-[500px]">
            <DialogHeader>
              <DialogTitle>Add New Dentist</DialogTitle>
            </DialogHeader>
            <form onSubmit={handleAddDentist} className="space-y-4">
              <div className="grid grid-cols-2 gap-4">
                <div className="space-y-2">
                  <Label htmlFor="firstName">First Name</Label>
                  <Input id="firstName" placeholder="John" required />
                </div>
                <div className="space-y-2">
                  <Label htmlFor="lastName">Last Name</Label>
                  <Input id="lastName" placeholder="Smith" required />
                </div>
              </div>
              
              <div className="space-y-2">
                <Label htmlFor="specialty">Specialty</Label>
                <Select required>
                  <SelectTrigger>
                    <SelectValue placeholder="Select specialty" />
                  </SelectTrigger>
                  <SelectContent>
                    {specialties.map((specialty) => (
                      <SelectItem key={specialty} value={specialty}>
                        {specialty}
                      </SelectItem>
                    ))}
                  </SelectContent>
                </Select>
              </div>

              <div className="grid grid-cols-2 gap-4">
                <div className="space-y-2">
                  <Label htmlFor="phone">Phone</Label>
                  <Input id="phone" placeholder="(555) 123-4567" required />
                </div>
                <div className="space-y-2">
                  <Label htmlFor="experience">Experience (years)</Label>
                  <Input id="experience" type="number" placeholder="5" required />
                </div>
              </div>

              <div className="space-y-2">
                <Label htmlFor="email">Email</Label>
                <Input id="email" type="email" placeholder="doctor@dentalclinic.com" required />
              </div>

              <div className="space-y-2">
                <Label htmlFor="schedule">Schedule</Label>
                <Input id="schedule" placeholder="Mon-Fri: 9AM-5PM" required />
              </div>

              <div className="flex justify-end space-x-2">
                <Button type="button" variant="outline" onClick={() => setIsDialogOpen(false)}>
                  Cancel
                </Button>
                <Button type="submit">Add Dentist</Button>
              </div>
            </form>
          </DialogContent>
        </Dialog>
      </div>

      {/* Search Bar */}
      <div className="flex items-center space-x-4">
        <div className="flex-1">
          <Input
            placeholder="Search dentists by name or specialty..."
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
            className="max-w-sm"
          />
        </div>
        <Badge variant="secondary">
          {filteredDentists.length} dentists found
        </Badge>
      </div>

      {/* Dentists Grid */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
        {filteredDentists.map((dentist) => (
          <Card key={dentist.id} className="hover:shadow-lg transition-shadow">
            <CardHeader>
              <div className="flex justify-between items-start">
                <CardTitle className="text-lg">{dentist.name}</CardTitle>
                <Badge variant={getStatusColor(dentist.status)}>
                  {dentist.status}
                </Badge>
              </div>
              <p className="text-sm text-gray-600 font-medium">{dentist.specialty}</p>
            </CardHeader>
            <CardContent className="space-y-4">
              <div className="grid grid-cols-2 gap-4 text-sm">
                <div>
                  <p className="text-gray-600">Experience</p>
                  <p className="font-medium">{dentist.experience}</p>
                </div>
                <div>
                  <p className="text-gray-600">Patients</p>
                  <p className="font-medium">{dentist.patients}</p>
                </div>
                <div>
                  <p className="text-gray-600">Rating</p>
                  <p className="font-medium">{dentist.rating}/5.0</p>
                </div>
                <div>
                  <p className="text-gray-600">Schedule</p>
                  <p className="font-medium text-xs">{dentist.schedule}</p>
                </div>
              </div>

              <div className="text-sm">
                <p className="text-gray-600 mb-1">Contact</p>
                <p className="font-medium">{dentist.phone}</p>
                <p className="text-gray-600 text-xs">{dentist.email}</p>
              </div>

              <div>
                <p className="text-sm text-gray-600 mb-2">Qualifications:</p>
                <div className="flex flex-wrap gap-1">
                  {dentist.qualifications.map((qualification, index) => (
                    <Badge key={index} variant="outline" className="text-xs">
                      {qualification}
                    </Badge>
                  ))}
                </div>
              </div>

              <div className="flex space-x-2 pt-2">
                <Button size="sm" variant="outline" className="flex-1">
                  View Schedule
                </Button>
                <Button size="sm" className="flex-1">
                  Edit Profile
                </Button>
              </div>
            </CardContent>
          </Card>
        ))}
      </div>

      {filteredDentists.length === 0 && (
        <Card>
          <CardContent className="text-center py-8">
            <p className="text-gray-500">No dentists found matching your search.</p>
          </CardContent>
        </Card>
      )}
    </div>
  );
}
